:root {
    --background-color: #f5f5f5;
    --text-color: #2c3e50;
    --banner-background: linear-gradient(163.85deg, #1d2129 0%, #00308f 100%);
    --input-background: #ffffff;
    --input-border-color: #dcdcdc;
    --button-background: #3498db;
    --button-text-color: #ffffff;
    --button-hover-background: #2980b9;
    --error-text-color: #e74c3c;
    --link-color: #2980b9;
    --shadow-color: rgba(0, 0, 0, 0.1);
}

[data-theme="dark"] {
    --background-color: #1e1e1e;
    --input-background: #333333;
    --input-text-color: #ecf0f1;
    --input-border-color: #555555;
    --input-autofill-background: #333333;
    --input-autofill-text-color: #ecf0f1;
    --input-focus-background: #444444;
    --input-focus-text-color: #ecf0f1;

    input,
    textarea {
        background-color: var(--input-background);
        color: var(--input-text-color);
        border: 1px solid var(--input-border-color);

        &::placeholder {
            color: var(--input-text-color);
        }

        &:hover,
        &:focus {
            background-color: var(--input-focus-background);
            /* 确保在 hover 和 focus 时背景色一致 */
            color: var(--input-focus-text-color);
            outline: none;
            box-shadow: 0 0 0 2px var(--input-border-color);
        }

        &:-webkit-autofill,
        &:-webkit-autofill:hover,
        &:-webkit-autofill:focus {
            -webkit-box-shadow: 0 0 0px 1000px var(--input-autofill-background) inset;
            /* 修复 autofill 背景色 */
            -webkit-text-fill-color: var(--input-autofill-text-color);
            /* 修复 autofill 文本颜色 */
            transition: background-color 5000s ease-in-out 0s;
            /* 确保 autofill 的背景色不会被覆盖 */
        }
    }
}


.container {
    display: flex;
    height: 100vh;
    background-color: var(--background-color);
    color: var(--text-color);
    box-shadow: 0 0 10px var(--shadow-color);

    .banner {
        width: 550px;
        background: var(--banner-background);
        /* 使用线性渐变作为背景 */
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 20px;
    }


    .content {
        flex: 1;
        position: relative;
        padding: 40px;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .footer {
        width: 100%;
        position: absolute;
        bottom: 0;
        right: 0;
    }
}

.logo {
    position: fixed;
    top: 24px;
    left: 22px;
    display: inline-flex;
    align-items: center;
    z-index: 1;

    &-text {
        margin-left: 4px;
        margin-right: 4px;
        font-size: 20px;
        color: var(--text-color);
    }
}

.banner {
    display: flex;
    justify-content: center;
    align-items: center;

    &-inner {
        height: 100%;
        flex: 1;
    }
}

.content {
    display: flex;
    justify-content: center;
    align-items: center;
}

.carousel {
    display: flex;
    justify-content: center; // 水平居中
    align-items: center; // 垂直居中
    height: 100%; // 确保容器高度为100%
}

.carousel-item {
    display: flex;
    flex-direction: column;
    align-items: center; // 水平居中
    justify-content: center; // 垂直居中
    text-align: center; // 文字居中
}

.carousel-title {
    font-weight: 500;
    font-size: 20px;
    line-height: 28px;
    color: white;
    margin-bottom: 10px;
}

.carousel-sub-title {
    margin-bottom: 20px;
    color: #86909C;
}

.carousel-image {
    display: block; // 确保图片是块级元素
    margin: 0 auto; // 水平居中
    width: 320px;
}




.login-form {
    &-wrapper {
        width: 320px;
        background-color: var(--input-background);
        padding: 20px;
        border-radius: 8px;
        box-shadow: 0 0 15px var(--shadow-color);
    }

    &-title {
        font-size: 24px;
        font-weight: 600;
        color: var(--text-color);
        line-height: 32px;
        margin-bottom: 16px;
    }

    &-sub-title {
        font-size: 16px;
        line-height: 24px;
        color: var(--text-color);
        margin-bottom: 24px;
    }

    &-error-msg {
        height: 32px;
        line-height: 32px;
        color: var(--error-text-color);
    }

    &-password-actions {
        display: flex;
        justify-content: space-between;
        margin-bottom: 16px;
    }

    &-register-btn {
        color: var(--link-color) !important;
    }
}

.ant-input {
    background-color: var(--input-background);
    border: 1px solid var(--input-border-color);
    color: var(--text-color);
    border-radius: 4px;

    &:hover {
        border-color: var(--button-hover-background);
    }
}

.ant-btn-primary {
    background-color: var(--button-background);
    border-color: var(--button-background);
    color: var(--button-text-color);

    &:hover {
        background-color: var(--button-hover-background);
        border-color: var(--button-hover-background);
    }
}

.ant-form-item-label>label {
    color: var(--text-color);
}